import React from 'react'

export default function Contact() {
    return (
        <div className="cont" id="contact-page">
            <div className="box">
                <div className="left">
                    <div className="overlay">
                        <div className="part">
                            <h4>Address</h4>
                            <span>lots Mostaqbal, Sidi Maafa, Oujda, 60000 Morocco</span>
                        </div>
                        <div className="part">
                            <h4>Email</h4>
                            <span>afaithraf@gmail.com</span>
                        </div>
                        <div className="part">
                            <h4>Phone/Whatsapp</h4>
                            <span>+212630157611</span>
                        </div>

                    </div>
                </div>
                <div className="right">
                    <h2>Send Me A Message</h2>
                    <form>
                        <input type="text" name="Name" placeholder="Name" />
                        <input type="email" name="Email" placeholder="Email" />
                        <input type="text" name="Phone" placeholder="Phone Number" />
                        <textarea placeholder="Write me Something here..." /><br />
                        <button>Send</button>
                    </form>
                </div>
            </div>
            <style jsx>{`
            .cont {
                width: 100%;
                height: 100vh;
                position: relative;
                background: #111111b3;
               backdrop-filter: blur(25px);
               text-align: center;
               color: #ffffff;
               display: flex;
            }
            .box{
                height: 80%;
                width: 90vw;
                max-width: 1000px;
                background: #fff;
                margin: auto;
                display: grid;
                grid-template-rows: 100%;
                grid-template-columns: 50% 50%;
            }
            @media (max-width: 900px){
                .box{
                    grid-template-columns: 100%;
                    grid-template-rows: 50% 50%;
                    height: 1111px;
                }
                .box .left .overlay{
                    text-align: center;
                }
            }
            .box .left{
                background: url("https://afaithraf.github.io/my-portfolio//assets/cbg.jpg");
                background-size: cover;
                padding: 0;
            }
            .overlay{
                background: #00000090;
                height: 100%;
                width: 100%;
                padding: 0;
                margin: 0;
                display: flex;
                flex-direction: column;
                text-align: left;
                justify-content: center;
            }
            .left h4{
                color: #fff;
                margin-top: 0;
                font-size: 1.2em;
            }
            .part{
                margin: 30px 80px;
                color: #e1e1e1;
            }
            .right{
                color: #010101;
                display: flex;
                flex-direction: column;
                height: 100%;
                justify-content: space-evenly;
            }
            .right h2{
            }
            .right input{
                height: 40px;
                width: 80%;
                margin: 10px;
                padding: 10px;
            }
            .right textarea{
                min-width: 80%;
                max-width: 80%;
                height: 200px;
                margin: 10px;
                padding: 10px;
            }
            .right button{
                background: #ffff00;
                border: 0;
                color: #010101;
                padding: 10px 20px;
                float: right;
                margin: 10px 40px;
                font-size: 1em;
            }
            `}</style>
        </div>
    )
}
